<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
      <el-breadcrumb-item :to="{path:'/mainTwo'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>物业信息管理</el-breadcrumb-item>
      <el-breadcrumb-item>维修管理</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="el-card">

      <el-row :gutter="10">
        <el-col :span="8">
          <el-input placeholder="请输入维修事件编号" v-model="repairNumber">
            <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true">添加维修</el-button>
        </el-col>
      </el-row>

      <el-table :data="tableData">
        <el-table-column prop="id" label="序号" width="70"></el-table-column>
        <el-table-column prop="repairNumber" label="维修编号" width="120"></el-table-column>
        <el-table-column prop="repairEvent" label="维修事件" width="140"></el-table-column>
        <el-table-column prop="repairAddress" label="维修地点" width="140"></el-table-column>
        <el-table-column prop="repairState" label="维修状态" width="120"></el-table-column>
        <el-table-column prop="repairTime" label="报修时间" width="120"></el-table-column>
        <el-table-column label="操作" min-width="250">
          <template slot-scope="scope">
            <el-button type="primary" @click="showReadDialog(scope.row.id)" icon="el-icon-reading">查看</el-button>
            <el-button type="warning" @click="showEditDialog(scope.row.id)" icon="el-icon-edit">修改</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="removeRepairById(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total,prev, pager, next"
        :total="total">
      </el-pagination>
    </el-card>

    <el-dialog
      title="添加信息"
      :visible.sync="addDialogVisible"
      width="40%"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px" @close="addDialogClosed">
        <el-form-item label="维修编号" prop="repairNumber">
          <el-input v-model="addForm.repairNumber"></el-input>
        </el-form-item>
        <el-form-item label="维修事件" prop="repairEvent">
          <el-input v-model="addForm.repairEvent"></el-input>
        </el-form-item>
        <el-form-item label="维修地点" prop="repairAddress">
          <el-input v-model="addForm.repairAddress"></el-input>
        </el-form-item>
        <el-form-item label="维修状态" prop="repairState">
          <el-input v-model="addForm.repairState"></el-input>
        </el-form-item>
        <el-form-item label="报修时间" prop="repairTime">
          <el-input v-model="addForm.repairTime"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRepair">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改维修信息"
      :visible.sync="editDialogVisible"
      width="40%"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px" @close="editDialogClosed">
        <el-form-item label="维修编号" prop="repairNumber">
          <el-input v-model="editForm.repairNumber"></el-input>
        </el-form-item>
        <el-form-item label="维修事件" prop="repairEvent">
          <el-input v-model="editForm.repairEvent"></el-input>
        </el-form-item>
        <el-form-item label="维修地点" prop="repairAddress">
          <el-input v-model="editForm.repairAddress"></el-input>
        </el-form-item>
        <el-form-item label="维修状态" prop="repairState">
          <el-input v-model="editForm.repairState"></el-input>
        </el-form-item>
        <el-form-item label="报修时间" prop="repairTime">
          <el-input v-model="editForm.repairTime"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editRepair">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="查看"
      :visible.sync="readDialogVisible"
      width="40%"
    >
      <el-form :model="readForm" ref="editFormRef" label-width="90px" >
        <el-form-item label="维修编号" prop="repairNumber">
          <el-input v-model="readForm.repairNumber" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="维修事件" prop="repairEvent">
          <el-input v-model="readForm.repairEvent" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="维修地点" prop="repairAddress">
          <el-input v-model="readForm.repairAddress" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="维修状态" prop="repairState">
          <el-input v-model="readForm.repairState" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="报修时间" prop="repairTime">
          <el-input v-model="readForm.repairTime" :disabled="true"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="readDialogVisible = false">关  闭</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    name: "repairManageTwo",
    data() {
      return {
        pageSize: 8,
        currentPage: 1,
        tableData: {
          id:"",
          repairNumber:"",
          repairEvent:"",
          repairAddress:"",
          repairState:"",
          repairTime:""
        },
        total: 0,
        repairNumber: "",
        addDialogVisible: false,
        addForm:{
          id:"",
          repairNumber:"",
          repairEvent:"",
          repairAddress:"",
          repairState:"",
          repairTime:""
        },
        addFormRules:{
          repairNumber:[
            {required: true, message: '维修编号不可为空', trigger: 'blur'},
            {min:1,max:20,message: '设备编号长度在2~20个字符之间',trigger:'blur'}
          ],
          repairEvent:[
            {required: true, message: '维修事件不可为空', trigger: 'blur'}
          ],
          repairAddress:[
            {required: true, message: '维修地点不可为空', trigger: 'blur'}
          ],
          repairState:[
            {required: true, message: '维修状态不可为空', trigger: 'blur'}
          ],
          repairTime:[
            {required: true, message: '报修时间不可为空', trigger: 'blur'}
          ]
        },
        editDialogVisible:false,
        editForm:{
          id:"",
          repairNumber:"",
          repairEvent:"",
          repairAddress:"",
          repairState:"",
          repairTime:""
        },
        editFormRules:{
          repairNumber:[
            {required: true, message: '维修编号不可为空', trigger: 'blur'},
            {min:1,max:20,message: '设备编号长度在2~20个字符之间',trigger:'blur'}
          ],
          repairEvent:[
            {required: true, message: '维修事件不可为空', trigger: 'blur'}
          ],
          repairAddress:[
            {required: true, message: '维修地点不可为空', trigger: 'blur'}
          ],
          repairState:[
            {required: true, message: '维修状态不可为空', trigger: 'blur'}
          ],
          repairTime:[
            {required: true, message: '报修时间不可为空', trigger: 'blur'}
          ]
        },
        readDialogVisible:false,
        readForm:{
          id:"",
          repairNumber:"",
          repairEvent:"",
          repairAddress:"",
          repairState:"",
          repairTime:""
        },
      }
    },
    created() {
      this.queryRepairList()
    },
    methods: {
      queryRepairList() {
        this.$http.get('repair/queryRepair',
          {params: {currentPage: this.currentPage, pageSize: this.pageSize, repairNumber: this.repairNumber}})
          .then(res => {
            this.total = res.data.total;
            this.tableData = res.data.data;
          });
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage
        this.queryRepairList()
      },
      handleSearch() {
        this.currentPage = 1
        this.queryRepairList()
      },
      addDialogClosed(){
        this.$refs.addFormRef.resetFields()
      },
      addRepair(){
        this.$refs.addFormRef.validate(async valid=>{
          if(!valid) return
          const {data:res}=await this.$http.post("repair/addRepair",this.addForm)
          if(res.code!==200){
            this.$message.error("添加维修信息失败,请检查维修信息")
          }else {
            this.$message.success("添加维修信息成功")
            this.addDialogVisible=false
            this.queryRepairList()
          }
        })
      },
      showEditDialog(id){
        this.editDialogVisible =true
        this.$http.get('repair/getRepairById',{params:{id:id}})
          .then(res => {
            this.editForm = res.data;
          });
      },
      editDialogClosed(){
        this.$refs.editFormRef.resetFields()
      },
      editRepair(){
        this.$refs.editFormRef.validate(async valid =>{
          if(!valid) return
          const {data:res}=await this.$http.post("repair/editRepair",this.editForm)
          if(res.code!==200){
            this.$message.error("修改维修信息失败,请检查维修信息")
          }else {
            this.$message.success("修改维修信息成功")
            this.editDialogVisible=false
            this.queryRepairList()
          }
        })
      },
      removeRepairById(id){
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.get('repair/removeRepairById',{params:{id:id}});
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.queryRepairList();
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      showReadDialog(id){
        this.readDialogVisible =true
        this.$http.get('repair/getRepairById',{params:{id:id}})
          .then(res => {
            this.readForm = res.data;
            //this.tableData=res.data;
          });
      }
    }
  }
</script>

<style scoped lang="scss">
  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 17px;
  }

  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  }
</style>



